import { Button } from "reactstrap";
import React, { Component } from "react";
import { AiFillFacebook } from 'react-icons/ai';
import {CgMail} from "react-icons/cg"


export default class Loginbox extends Component {
  render() {
    return (
      
        <div id="logreg-forms">
            
          <form className="form-signin">
            <h1
              style={{ textAlign: "center" }}
              className="h3 mb-3 font-weight-normal"
            >
              {" "}
              Sign in
            </h1>
            <div className="social-login">
              <button className="btn facebook-btn social-btn" type="button">
                <span>
                  <AiFillFacebook size="25px"/> Sign in with Facebook
                </span>{" "}
              </button>
              <button className="btn google-btn social-btn" type="button">
                <span>
                  <CgMail size="25px"/> Sign in with Google+
                </span>{" "}
              </button>
            </div>
            <p style={{ textAlign: "center" }}> OR </p>
            <input
              type="email"
              id="inputEmail"
              className="form-control"
              placeholder="Email address"
              required=""
              autofocus=""
            />
            <input
              type="password"
              id="inputPassword"
              className="form-control"
              placeholder="Password"
              required=""
            />
            <Button className="social-one" color="danger" type="submit" block>
              Sign-in
            </Button>
            <a style={{ textAlign: "center" }} href="/" id="forgot_pswd">
              Forgot password?
            </a>

            <p style={{ textAlign: "center" }}>Don't have an account!</p>
            <Button
              className="social-one"
              color="primary"
              type="submit"
              block
              id="btn-signup"
            >
              Sign-up New Account
            </Button>
          </form>

          <form action="/reset/password/" className="form-reset">
            <input
              type="email"
              id="resetEmail"
              className="form-control"
              placeholder="Email address"
              required=""
              autofocus=""
            />

            <a href="/" id="cancel_reset">
              <i className="fas fa-angle-left"></i> Back
            </a>
          </form>

          <form action="/signup/" className="form-signup">
            <div className="social-login">
              <button className="btn facebook-btn social-btn" type="button">
                <span> Sign up with Facebook</span>{" "}
              </button>
            </div>
            <div className="social-login">
              <button className="btn google-btn social-btn" type="button">
                <span>Sign up with Google+</span>{" "}
              </button>
            </div>

            <p style={{ textAlign: "center" }}>OR</p>

            <input
              type="text"
              id="user-name"
              className="form-control"
              placeholder="Full name"
              required=""
              autofocus=""
            />
            <input
              type="email"
              id="user-email"
              className="form-control"
              placeholder="Email address"
              required
              autofocus=""
            />
            <input
              type="password"
              id="user-pass"
              className="form-control"
              placeholder="Password"
              required
              autofocus=""
            />
            <input
              type="password"
              id="user-repeatpass"
              className="form-control"
              placeholder="Repeat Password"
              required
              autofocus=""
            />

            <button className="btn btn-primary btn-block" type="submit">
              {" "}
              Sign Up
            </button>
            <a href="/" id="cancel_signup">
              Back
            </a>
          </form>
        </div>
      
    );
  }
}
